body {
  background: url('../imgs/background.jpg');
  background-color: #0b0b0b;
  font-family: 'Roboto', sans-serif;
  margin: 0 auto;
}

#header {
  width: 100vw;
  height: 150px;
  background-color: rgb(253, 250, 250);
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 4rem;
  box-shadow: 0px 3px 5px rgb(46, 90, 133);
  text-shadow: 2px 2px 3px #000;
}

.card-container {
  margin: 0 auto;
  width: 75%;
  display: grid;
  grid-template-columns: repeat(6, auto);
  row-gap: 50px;
  column-gap: 20px;
}

.servant-card {
  width: 200px;
  cursor: pointer;
  transition: 0.3s;
}

.servant-card:hover {
  transform: scale(1.1);
  box-shadow: 0px 0px 10px rgba(236, 173, 13, 0.3);
  box-shadow: 0px 0px 20px rgba(225, 174, 44);
}

#scoreboard {
  margin: 20px;
  color: #fff;
  font-size: 3rem;
  text-align: center;
  display: flex;
  justify-content: center;
  margin: 30px 0px;
}

#gameover-screen {
  width: 100vw;
  height: 100vh;
  background-color: rgba(0, 0, 0, 0.5);
  position: absolute;
  z-index: 99;
  left: 0;
  top: 0;
  display: flex;
  justify-content: center;
  align-items: center;
}

#gameover-box {
  color: #fff;
  width: 500px;
  height: 500px;
  display: flex;
  flex-direction: column;
  align-items: center;
}

#gameover-box h1 {
  font-size: 3rem;
}

button {
  margin: 10px;
  width: 125px;
  height: 75px;
  font-size: 1.1rem;
  border-radius: 5px;
  border: none;
  font-family: 'Roboto', sans-serif;
  cursor: pointer;
  transition: 0.5s;
}

button:hover {
  transform: scale(1.02);
  box-shadow: 0px 0px 5px #fff;
}

#symbol {
  width: 100px;
  height: 100px;
  margin-left: 20px;
  margin-right: 20px;
}

#score {
  margin-right: 50px;
}
